@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap');
*{
    padding: 0;
    margin: 0;
}
body{
    background-color: #f1f0f3;
}
.gridContainer{
    display: grid;
    grid-template-rows:;
    width: 100vw;
    position: fixed;
}
.container{
    background-color: #fff;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 5px rgb(0 0 0 / 70%);
}
.logoContainner{
    display: flex;
    align-items: center;
}
.logoContainner img{
    width: 70px;
    margin-right: 15px;
}
.logoContainner img:hover{
    cursor: pointer;
}
.logoContainner input{
    width: 120px;
    height: 30px;
    border-radius: 100px;
}
.nameCompany1{
    color: black;
    font-family: 'Rubik', sans-serif;
    font-size: 25px;
    text-align: left;
}
.nameCompany2{
    color: black;
    font-family: 'Rubik', sans-serif;
    font-size: 15px;
    text-align: left;
}
.search{
    border: solid 1px rgb(0, 0, 0);
    background-color: #fff;
    border-radius: 100px;
    display: flex;
    align-items: center;
    padding: 0 10px;
    margin-left: 40rem;
}
input[type=search]{
    border: none;
    outline: none;
    font-size: 15px;
    padding-left: 5px;
}
.menuContainer{
    display: flex;
    flex-direction: row;
    gap: 1rem;
    margin: 10px 0;
    padding-left: 55vw;
}
.menu{
    border-right: solid 2px #000;
    padding-right: 10px;
    font-weight: 500;
    display: flex;
    flex-direction: row;
}
.menu:hover{
    cursor: pointer;
    color: #036;
}
.menuIcon{
    margin-right: 5px;
}
.hamburger{
    display: none;
}
.hamburger:hover{
    cursor: pointer;
}
.conMobile{
    display: none;
}
.menuMobile{
    display: none;
}
.menuMobile:hover{
    cursor: pointer;
    color: #b5b4b6;
}
@media only screen and (max-width:1200px){
    .search{
        margin-left: 20rem;
    }
    .menuContainer{
        padding-left: 40vw;
    }
}
@media only screen and (max-width:900px){
    .container{
        gap: 20rem;
    }
    .search{
        display: none;
    }
    .menuContainer{
        display: none;
    }
    .hamburger{
        display: flex;
        font-size: 30px;
    }
    .conMobile{
        display: block;
        justify-content: center;
        width: 100vw;
        float: right;
        background-color: #fff;
        margin-top: 100px;
    }
    .menuMobile{
        display: flex;
        flex-direction: row;
        gap: 1rem;
        margin: 15px 50px;
    
    }
    .searchMobile{
        width: 180px;
        border: solid 1px rgb(0, 0, 0);
        background-color: #fff;
        border-radius: 100px;
        display: flex;
        align-items: center;
        padding: 5px 10px;
        margin: 20px 50px; 
    }
}
@media only screen and (max-width:650px){
    .container{
        gap: 10rem;
    }
}
@media only screen and (max-width:500px){
    .container{
        gap: 5rem;
    }
}